/* label.checkbox:first-of-type {
    margin: 21px auto 20px;
} */

.checkbox {
    display: block;
    margin: 0 auto 20px;
    position: relative;
    padding-left: 45px;
    padding-top: 10px;
    width: 279px;
    height: 45px;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    font-family: 'Gilroy_Light';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 26px;
    color: #242424;
}

.checkbox:last-child {
    margin: 0 auto;
}

/* Hide the browser's default radio button */
.checkbox input {
    display: none;
}

/* Create a custom radio button */
.checkmark {
    position: absolute;
    top: 50%;
    left: 8%;
    height: 20px;
    width: 20px;
    background-image: radial-gradient(circle, #F4F4F4 0%, #F4F4F4 55%, #FFFFFF 55%, #FFFFFF 100%);
    border-radius: 50%;
    border: 0.5px solid #F4F4F4;
    transform: translate(-50%, -50%);
    box-sizing: border-box;
}

.checkboxContainer5 .checkmark {
    left: 13%;
}

.checkbox:hover input ~ .checkmark {
    background-color: #ccc;
}

.checkbox input:checked ~ .checkmark {
    background-image: radial-gradient(circle, #F4F4F4 0%, #F4F4F4 55%, #FFFFFF 55%, #FFFFFF 100%);
}

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

.checkbox input:checked ~ .checkmark:after {
    display: block;
}

.checkbox .checkmark:after {
    top: 50%;
    left: 52%;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: linear-gradient(180deg, #FEE9A1 0%, #FFDD64 100%);
    transform: translate(-50%, -50%);
}

